import { Component, OnInit } from '@angular/core';

// import TensorFlow.js as tfjs
import * as tf from '@tensorflow/tfjs';
import * as tmImage from '@teachablemachine/image';
import '@tensorflow/tfjs-backend-webgl';
import '@tensorflow/tfjs-backend-cpu';
//import COCO-SSD model as cocoSSD
import * as cocoSSD from '@tensorflow-models/coco-ssd';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  count: number = 0;
  private video: HTMLVideoElement;
  // the link to your model provided by Teachable Machine export panel
  URL = 'https://teachablemachine.withgoogle.com/models/onuy11n_f/';
  ngOnInit() {
    this.webcam_init();
  }

  public async predictWithCocoModel() {
    // const model = await cocoSSD.load('yolov7');
    // load model yolov7
    const model = await cocoSSD.load();
    // check if file exists in the directory ./model/model.json
    const modelURL = this.URL + 'model.json';
    const metadataURL = this.URL + 'metadata.json';

    const model2 = await tmImage.load(modelURL, metadataURL);

    this.detectFrame(this.video, model, model2);
    console.log('model loaded');
  }

  webcam_init() {
    this.video = <HTMLVideoElement>document.getElementById('vid');

    navigator.mediaDevices
      .getUserMedia({
        video: { width: 640, height: 480 },
        audio: false,
      })
      .then((stream) => {
        console.log('stream', stream);

        this.video.srcObject = stream;
        // wait for `loadeddata` event on the <video> element.
        this.video.addEventListener('loadeddata', (e) => {
          console.log(e);

          setTimeout(() => {
            console.log('start');

            this.video.play();
          }, 2000);
        });
        this.predictWithCocoModel();
      })
      .catch((err) => {
        console.log('error Webcam: ' + err);
      });
  }

  detectFrame = (video, model, model2) => {
    model.detect(video).then(async (predictions) => {
      console.log(predictions);
      var test = await model2.predict(video).then((predictions2) => {
        console.log(predictions2);
        return predictions2;
      });
      this.renderPredictions(predictions, test);
      requestAnimationFrame(() => {
        this.detectFrame(video, model, model2);
      });
    });
  };

  renderPredictions = (predictions, predictions2) => {
    const canvas = <HTMLCanvasElement>document.getElementById('canvas');

    const ctx = canvas.getContext('2d');

    canvas.width = 640;
    canvas.height = 480;

    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

    // Font options.
    const font = '16px sans-serif';
    ctx.font = font;
    ctx.textBaseline = 'top';
    ctx.drawImage(this.video, 0, 0, 640, 480);

    predictions.forEach((prediction) => {
      // create a roi line
      ctx.beginPath();
      ctx.moveTo(0, canvas.height / 2);
      ctx.lineTo(canvas.width, canvas.height / 2);
      ctx.lineWidth = 2;
      ctx.strokeStyle = 'red';
      ctx.stroke();
    });

    predictions.forEach((prediction) => {
      if (prediction.class == 'person' || prediction.class == 'bottle') {
        const x = prediction.bbox[0];
        const y = prediction.bbox[1];
        const width = prediction.bbox[2];
        const height = prediction.bbox[3];
        // Draw the bounding box.
        ctx.strokeStyle = '#00FF00';
        ctx.lineWidth = 2;
        ctx.strokeRect(x, y, width, height);
        // Draw the label background.
        ctx.fillStyle = '#00FF00';
        const textWidth = ctx.measureText(
          prediction.class + Math.round(prediction.score * 100) + '%'
        ).width;
        predictions2.forEach((prediction) => {
          if (prediction.className) {
            const textWidth = ctx.measureText(
              prediction.className +
                Math.round(prediction.probability * 100) +
                '%'
            ).width;
            const textHeight = parseInt(font, 10); // base 10
            ctx.fillRect(x, y + 15, textWidth + 8, textHeight + 8); // if (prediction.class === "person")
          }
        });
        const textHeight = parseInt(font, 10); // base 10
        ctx.fillRect(x, y, textWidth + 4, textHeight + 4); // if (prediction.class === "person")
      }
    });

    predictions.forEach((prediction) => {
      if (prediction.class == 'person' || prediction.class == 'bottle') {
        const x = prediction.bbox[0];
        const y = prediction.bbox[1];
        // Draw the text last to ensure it's on top.
        ctx.fillStyle = '#000000';
        const className =
          prediction.class.charAt(0).toUpperCase() + prediction.class.slice(1);
        ctx.fillText(
          className + ' ' + Math.round(prediction.score * 100) + '%',
          x,
          y
        );
        predictions2.forEach((prediction) => {
          if (prediction.probability > 0.5) {
            ctx.fillText(
              prediction.className +
                ' ' +
                Math.round(prediction.probability * 100) +
                '%',
              x,
              y + 15
            );
          }
        });
      }
    });
  };
}
